<template>
  <div class="Sidebar">
    <div class="setUp">
      <div class="tbas" ref="tbas">
        <div class="tbas_head">
          <p>{{ headText.title }}</p>
          <span>{{ headText.Introduction ?? '暂无内容' }}</span>
        </div>
        <div class="tbasLeft">
          <div class="tbasLeft_activeBar" :style="{ transform: `translateY(${LineTop})` }"></div>
          <div class="tbasLeftBox" v-for="(item, index) in Option"
            :style="{ color: activeValue == index ? '#409eff' : '#303133' }" @click="active(index)">
            <div class="tbasLeftBox_icon" v-html="item.icon"></div>
            {{ item.title }}
          </div>
        </div>
        <div class="tbasRight">
          <div class="tbasRight_body">
            <!-- 设置 -->
            <div v-if="activeValue == 0">
              暂无内容
            </div>
            <!-- 发现 -->
            <div v-if="activeValue == 1">
              <div class="tbasRight_Box ncolor">
                目前正在开发
              </div>
              <div class="tbasRight_Box">
                <span class="btn" @click="$emit('findDrawer')">
                  前往发现页面
                </span>
              </div>
            </div>
            <!-- 备份与恢复 -->
            <div v-if="activeValue == 2">
              <div class="tbasRight_Box ncolor">
                将配置以文件形式保存在您的设备上
              </div>
              <div class="tbasRight_Box">
                <span class="btn" @click="$emit('backup')">
                  导出
                </span>
              </div>
              <div class="tbasRight_Box ncolor">
                还原之前的备份
              </div>
              <div class="tbasRight_Box">
                <span class="btn" @click="$emit('restore')">
                  恢复
                </span>
              </div>
              <div class="tbasRight_Box ncolor">
                重置之后页面数据将与<span style="color: #409eff;">发现</span>页面保持同步
              </div>
              <div class="tbasRight_Box btn_center">
                <span @click="resetData">
                  重置所有数据
                </span>
              </div>
            </div>
            <!-- 关于 -->
            <div v-if="activeValue == 3">
              <div class="tbasRight_Box ncolor">
                联系我
              </div>
              <div class="tbasRight_Box2">
                <div class="tbasRight_Box qq">
                  <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-QQ"></use>
                  </svg>
                  QQ
                </div>
                <div class="tbasRight_Box weixin">
                  <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-weixin"></use>
                  </svg>
                  微信
                </div>
                <div class="tbasRight_Box gthub" @click="openGithub">
                  <svg class="icon" style="width: 14px;" aria-hidden="true">
                    <use xlink:href="#icon-github"></use>
                  </svg>
                  <span>
                    Github
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="tipArrow">
      <el-icon>
        <CaretLeft />
      </el-icon>
    </div>
  </div>
</template>
<script setup>
import { ref, onMounted, computed, reactive, nextTick, watchEffect, toRef, toRefs, watch, onUnmounted } from 'vue';
import { useStore } from 'vuex';
import { ElMessageBox, ElMessage } from 'element-plus'
import webData from '../untils/websiteData.js'
import IndexDB from '../untils/indexDB.js'
defineEmits(['backup', 'restore', 'findDrawer'])
const store = useStore()
//#region 标签点击效果
const tbas = ref(null);
const activeValue = ref(1);
const LineTop = ref(activeValue.value * 40 + 'px');
function active(value) {
  activeValue.value = value;
  LineTop.value = value * 40 + 'px'
  headText.value = Option[value]
}
let Option = [

  { title: '设置', Introduction: "暂无内容", icon: '<svg viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" data-v-ea893728=""><path fill="currentColor" d="M600.704 64a32 32 0 0 1 30.464 22.208l35.2 109.376c14.784 7.232 28.928 15.36 42.432 24.512l112.384-24.192a32 32 0 0 1 34.432 15.36L944.32 364.8a32 32 0 0 1-4.032 37.504l-77.12 85.12a357.12 357.12 0 0 1 0 49.024l77.12 85.248a32 32 0 0 1 4.032 37.504l-88.704 153.6a32 32 0 0 1-34.432 15.296L708.8 803.904c-13.44 9.088-27.648 17.28-42.368 24.512l-35.264 109.376A32 32 0 0 1 600.704 960H423.296a32 32 0 0 1-30.464-22.208L357.696 828.48a351.616 351.616 0 0 1-42.56-24.64l-112.32 24.256a32 32 0 0 1-34.432-15.36L79.68 659.2a32 32 0 0 1 4.032-37.504l77.12-85.248a357.12 357.12 0 0 1 0-48.896l-77.12-85.248A32 32 0 0 1 79.68 364.8l88.704-153.6a32 32 0 0 1 34.432-15.296l112.32 24.256c13.568-9.152 27.776-17.408 42.56-24.64l35.2-109.312A32 32 0 0 1 423.232 64H600.64zm-23.424 64H446.72l-36.352 113.088-24.512 11.968a294.113 294.113 0 0 0-34.816 20.096l-22.656 15.36-116.224-25.088-65.28 113.152 79.68 88.192-1.92 27.136a293.12 293.12 0 0 0 0 40.192l1.92 27.136-79.808 88.192 65.344 113.152 116.224-25.024 22.656 15.296a294.113 294.113 0 0 0 34.816 20.096l24.512 11.968L446.72 896h130.688l36.48-113.152 24.448-11.904a288.282 288.282 0 0 0 34.752-20.096l22.592-15.296 116.288 25.024 65.28-113.152-79.744-88.192 1.92-27.136a293.12 293.12 0 0 0 0-40.256l-1.92-27.136 79.808-88.128-65.344-113.152-116.288 24.96-22.592-15.232a287.616 287.616 0 0 0-34.752-20.096l-24.448-11.904L577.344 128zM512 320a192 192 0 1 1 0 384 192 192 0 0 1 0-384zm0 64a128 128 0 1 0 0 256 128 128 0 0 0 0-256z"></path></svg>' },
  { title: '发现', Introduction: "该页面正在开发中", icon: '<svg viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" data-v-ea893728=""><path fill="currentColor" d="M512 896a384 384 0 1 0 0-768 384 384 0 0 0 0 768zm0 64a448 448 0 1 1 0-896 448 448 0 0 1 0 896z"></path><path fill="currentColor" d="M725.888 315.008C676.48 428.672 624 513.28 568.576 568.64c-55.424 55.424-139.968 107.904-253.568 157.312a12.8 12.8 0 0 1-16.896-16.832c49.536-113.728 102.016-198.272 157.312-253.632 55.36-55.296 139.904-107.776 253.632-157.312a12.8 12.8 0 0 1 16.832 16.832z"></path></svg>' },
  { title: '备份与恢复', Introduction: "可以在这里备份或恢复您的配置", icon: '<svg viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" data-v-ea893728=""><path fill="currentColor" d="M832 384H576V128H192v768h640V384zm-26.496-64L640 154.496V320h165.504zM160 64h480l256 256v608a32 32 0 0 1-32 32H160a32 32 0 0 1-32-32V96a32 32 0 0 1 32-32zm160 448h384v64H320v-64zm0-192h160v64H320v-64zm0 384h384v64H320v-64z"></path></svg>' },
  { title: '关于', Introduction: "如果有什么建议或想法欢迎联系我", icon: '<svg viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" data-v-ea893728=""><path fill="currentColor" d="M512 64a448 448 0 1 1 0 896.064A448 448 0 0 1 512 64zm67.2 275.072c33.28 0 60.288-23.104 60.288-57.344s-27.072-57.344-60.288-57.344c-33.28 0-60.16 23.104-60.16 57.344s26.88 57.344 60.16 57.344zM590.912 699.2c0-6.848 2.368-24.64 1.024-34.752l-52.608 60.544c-10.88 11.456-24.512 19.392-30.912 17.28a12.992 12.992 0 0 1-8.256-14.72l87.68-276.992c7.168-35.136-12.544-67.2-54.336-71.296-44.096 0-108.992 44.736-148.48 101.504 0 6.784-1.28 23.68.064 33.792l52.544-60.608c10.88-11.328 23.552-19.328 29.952-17.152a12.8 12.8 0 0 1 7.808 16.128L388.48 728.576c-10.048 32.256 8.96 63.872 55.04 71.04 67.84 0 107.904-43.648 147.456-100.416z"></path></svg>' },
]
const headText = ref(Option[activeValue.value]);

//#endregion
// 点击重置按钮
const resetData = function () {
  ElMessageBox.confirm(
    `<p>你确定要重置全部数据吗?
        </br><span style="color:red">注意！此操作会删除所有本地数据！</span>
      </p>`,
    '警告',
    {
      type: 'warning',
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      dangerouslyUseHTMLString: true,
    }).then(async () => {
      const arreyValue = JSON.parse(JSON.stringify(webData.webArry));
      store.commit('setData', arreyValue)
      const dbName = 'DouDouDB', storeName = 'website';
      IndexDB.deleteDBAll(dbName)
      ElMessage({
        message: '数据重置成功',
        type: 'success',
      })
    }).catch(() => { })
}
// 跳转
function openGithub() {
  window.open('https://github.com/DouDouLi-YouTang')
}
</script>
<style lang="less" scoped>
@height: 100%;
/*默认未展开的宽度*/
@SidebarWidth: 20px;

/*手机端展开后的宽度*/


.CustomAnimation {
  transition: all .5s;
}

.Sidebar {
  width: @SidebarWidth;
  height: 100vh;
  position: absolute;
  top: 0;
  right: 0;
  overflow: hidden;
  .CustomAnimation();
  z-index: 10;


  &:hover {
    width: 400px;
    box-shadow: 0px 0px 12px rgba(0, 0, 0, .12);

    .setUp {
      transform: translateX(0);
      opacity: 1;
    }
  }

  .setUp {
    width: 400px;
    transition: opacity .5s, transform .5s;
    transition-timing-function: 'ease-in-out';
    align-items: center;
    height: 100%;
    background-color: white;
    transform: translateX(20px);

    .tbas {
      height: @height;

      .tbas_head {
        height: 80px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-left: 20px;
        border-bottom: 1px solid #dcdfe6;
        margin-bottom: 10px;
        overflow: hidden;

        p {
          font-size: 20px;
          line-height: 30px;
        }

        span {
          color: #909399;
          font-size: 14px;
        }
      }

      .tbasLeft {
        float: left;
        position: relative;
        height: 100%;
        padding-top: calc(25vh - 70px);

        .tbasLeft_activeBar {
          height: 40px;
          width: 2px;
          position: absolute;
          background-color: #409eff;
          z-index: 10;
          right: 0;
          transition: all .2s ease-in-out;
        }

        .tbasLeftBox {
          display: flex;
          height: 40px;
          padding: 0 15px;
          align-items: center;
          cursor: pointer;
          justify-content: flex-start;
          transition: all .2s ease-in-out;
          font-size: 14px;

          .tbasLeftBox_icon {
            margin-right: 5px;
            height: 16px;
            width: 16px;
          }


          &:hover {
            color: #409eff;
            background-color: #ecf5ff;
          }
        }

        // &::after {
        //   content: "";
        //   width: 2px;
        //   height: 100%;
        //   background-color: #e4e7ed;
        //   position: absolute;
        //   right: 0;
        //   top: 0;
        // }
      }



      .tbasRight {
        height: @height;
        overflow: auto;
        height: calc(@height - 100px);
        scrollbar-width: none;
        -ms-overflow-style: none;
        background-color: #f1f0f5;
        border-radius: 10px;
        padding: 10px;

        &::-webkit-scrollbar {
          width: 0 !important
        }

        .tbasRight_body {

          .tbasRight_Box {
            background-color: white;
            border-radius: 5px;
            padding: 10px 15px;
            font-size: 14px;
            margin-bottom: 20px;

            .btn {
              color: #409eff;
              cursor: pointer;
            }


          }

          .tbasRight_Box2 {
            position: relative;

            .tbasRight_Box {
              margin-right: 5px;
              margin-bottom: 10px;
              float: left;
              width: calc(50% - 10px);
              cursor: pointer;

              &:hover::after {
                position: absolute;
                content: "";
                width: 80px;
                height: 80px;
                top: 40px;
                left: 0;
                background-position: center center;
                background-size: cover;
                padding: 10px;
              }
            }

            .qq:hover::after {
              background-image: url(../assets/img/QQ.png);
            }

            .weixin:hover::after {
              background-image: url(../assets/img/weixin.png);
              left: calc(50% - 10px);
            }

            .gthub {
              background-color: #303133;
              color: white;
            }
          }

          .ncolor {
            background-color: inherit;
            margin-bottom: 0;
            padding-bottom: 5px;
          }

          .btn_center {
            text-align: center;
            cursor: pointer;
            color: #F56C6C;
          }
        }
      }

    }
  }

  .tipArrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    .CustomAnimation();
  }


  &:hover .tipArrow {
    opacity: 0;
  }
}

@media screen and (max-width: 1920px) {}

/*当页面最大宽小于450*/
@media only screen and (max-width: 450px) {

  .Sidebar {
    width: 20px;

    &:hover {
      width: 300px;
    }

    .setUp {
      width: 300px;

      .tbas {
        .tbas_head {
          p {
            font-size: 16px;
          }

          span {
            font-size: 12px;
          }
        }

        .tbasLeft {
          .tbasLeftBox {
            font-size: 12px;
            padding: 0 10px;
          }
        }

        .tbasRight {


          .tbasRight_body {
            .tbasRight_Box {
              font-size: 12px;
              padding: 10px 10px;
              margin-bottom: 10px;
            }

            .ncolor {
              background-color: inherit;
              margin-bottom: 0;
              padding-bottom: 5px;
            }
          }
        }
      }

    }

  }
}
</style>